/* Layout */
header {
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  background: #fafafa;
  border-bottom: 1px solid #ddd;
}

.logo {
  font-weight: bold;
  font-size: 1.25rem;
  margin-right: auto;
}

header input {
  padding: 0.4rem;
  font-size: 1rem;
  width: 200px;
}

.layout {
  display: flex;
  min-height: calc(100vh - 50px);
}

.sidebar {
  width: 200px;
  background: #f4f4f4;
  padding: 1rem;
  border-right: 1px solid #ddd;
}

.sidebar ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.sidebar li {
  padding: 0.5rem;
  margin-bottom: 0.25rem;
  cursor: pointer;
  border-radius: 4px;
}

.sidebar li.active,
.sidebar li:hover {
  background: #ddd;
}

.content {
  flex: 1;
  padding: 1rem;
}

/* Q&A Card */
.qa-card {
  border: 1px solid #ccc;
  margin-bottom: 1rem;
  border-radius: 4px;
  overflow: hidden;
}

.q-header,
.sub-header {
  background: #eee;
  border: none;
  width: 100%;
  text-align: left;
  padding: 0.75rem 1rem;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  font-size: 1rem;
}

.sub-header {
  background: transparent;
  padding: 0.5rem 1rem;
}

.answer {
  padding: 0.5rem 1rem;
}

.sub-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

/* Toggle icon */
.toggle-icon {
  transition: transform 0.2s ease;
}

.toggle-icon.open {
  transform: rotate(90deg);
}

/* Markdown content styling */
.sub-answer {
  padding: 0.5rem 1rem;
  border-left: 2px solid #ccc;
  margin-bottom: 0.5rem;
}

.sub-answer h3,
.sub-answer h4 {
  margin: 0.5rem 0;
}

.sub-answer pre {
  background: #2d2d2d;
  color: #f8f8f2;
  padding: 0.75rem;
  border-radius: 4px;
  overflow-x: auto;
}

.sub-answer table {
  width: 100%;
  border-collapse: collapse;
  margin: 0.5rem 0;
}

.sub-answer table,
.sub-answer th,
.sub-answer td {
  border: 1px solid #999;
}

.sub-answer th,
.sub-answer td {
  padding: 0.3rem 0.6rem;
}

.sub-answer code {
  background: #eee;
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
  font-family: monospace;
}
